// KomoraLink API client for Corporate site
// Uses the main KomoraLink backend for user authentication

const KOMORALINK_API_URL = process.env.NEXT_PUBLIC_KOMORALINK_API_URL || 'https://dash.fortibtech.com';

export interface KomoraLoginResponse {
    access_token: string;
    user?: {
        id: string;
        email: string;
        firstName: string;
        lastName?: string;
        profileType: 'PARTICULIER' | 'COMMERCANT' | 'RESTAURATEUR' | 'LIVREUR' | 'FOURNISSEUR';
    };
}

export interface KomoraUserProfile {
    id: string;
    email: string;
    firstName: string;
    lastName?: string;
    profileType: 'PARTICULIER' | 'COMMERCANT' | 'RESTAURATEUR' | 'LIVREUR' | 'FOURNISSEUR';
}

/**
 * Login to KomoraLink with email and password
 */
export async function loginKomoraLink(email: string, password: string): Promise<KomoraLoginResponse> {
    const response = await fetch(`${KOMORALINK_API_URL}/auth/login`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
    });

    if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));

        if (response.status === 401) {
            if (errorData?.message === "Veuillez d'abord v√©rifier votre e-mail.") {
                throw new Error("Veuillez d'abord v√©rifier votre e-mail.");
            }
            throw new Error('Identifiants invalides');
        }

        throw new Error(errorData?.message || 'Erreur de connexion');
    }

    return response.json();
}

/**
 * Get user profile with token
 */
export async function getKomoraProfile(token: string): Promise<KomoraUserProfile> {
    const response = await fetch(`${KOMORALINK_API_URL}/auth/profile`, {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
        },
    });

    if (!response.ok) {
        throw new Error('Impossible de r√©cup√©rer le profil');
    }

    return response.json();
}
